import { FunctionComponent, Fragment } from 'preact';
import { OrderProductListContentProps } from '@/order/types';
import { Card, Header } from '@adobe-commerce/elsie/components';
import { useMemo } from 'preact/hooks';
import { CartSummaryItem, OrderProductListSkeleton } from '@/order/components';
import { useText } from '@adobe-commerce/elsie/i18n';
import { OrderItemModel } from '@/order/data/models';
import '@/order/components/OrderProductListContent/OrderProductListContent.css';
import { categorizeProducts } from '@/order/lib/categorizeProducts';

export const OrderProductListContent: FunctionComponent<
  OrderProductListContentProps
> = ({
  slots,
  placeholderImage,
  loading,
  taxConfig,
  order = null,
  withHeader = true,
  showConfigurableOptions,
  routeProductDetails,
}) => {
  const isReturnPage = !!order?.returnNumber;

  const translations = useText({
    cancelled: 'Order.OrderProductListContent.cancelledTitle',
    allOrders: 'Order.OrderProductListContent.allOrdersTitle',
    returned: 'Order.OrderProductListContent.returnedTitle',
    refunded: 'Order.OrderProductListContent.refundedTitle',
    sender: 'Order.OrderProductListContent.GiftCard.sender',
    recipient: 'Order.OrderProductListContent.GiftCard.recipient',
    message: 'Order.OrderProductListContent.GiftCard.message',
    outOfStock: 'Order.OrderProductListContent.stockStatus.outOfStock',
    downloadableCount: 'Order.OrderProductListContent.downloadableCount',
  });

  const itemsList = useMemo(() => {
    if (!order) return [];

    if (!isReturnPage) {
      const { returnedList, canceledItems, nonCanceledItems } =
        categorizeProducts(order);

      return [
        {
          type: 'returned',
          list: returnedList,
          title: translations.returned,
        },
        {
          type: 'cancelled',
          list: canceledItems,
          title: translations.cancelled,
        },
        {
          type: 'allItems',
          list: nonCanceledItems,
          title: translations.allOrders,
        },
      ].filter((section) => section?.list?.length > 0);
    }

    const returnItems =
      order.returns
        .find((returnData) => returnData.returnNumber === order?.returnNumber)
        ?.items?.map((el) => ({ ...el, totalQuantity: el.requestQuantity })) ??
      [];

    return [
      {
        type: 'returned',
        list: returnItems,
        title: translations.returned,
      },
    ];
  }, [order, isReturnPage, translations]);

  if (!order) return <OrderProductListSkeleton />;

  if (itemsList.every((el) => el.list.length === 0)) return null;

  return (
    <Card variant="secondary" className={'order-order-product-list-content'}>
      {itemsList.map((item, index) => {
        const headerTotalProductValue = item.list
          .filter((entry): entry is OrderItemModel => entry !== null)
          .reduce(
            (accumulator, { totalQuantity }) => totalQuantity + accumulator,
            0
          );

        return (
          <Fragment key={index}>
            {withHeader ? (
              <Header title={`${item.title} (${headerTotalProductValue})`} />
            ) : null}
            <ul className="order-order-product-list-content__items">
              {item.list
                ?.filter(
                  (product): product is OrderItemModel => product !== null
                )
                .map((product) => (
                  <li
                    data-testid="order-product-list-content-item"
                    key={product.id}
                  >
                    <CartSummaryItem
                      slots={slots}
                      placeholderImage={placeholderImage}
                      loading={loading}
                      product={product}
                      itemType={item.type}
                      taxConfig={taxConfig}
                      translations={translations}
                      showConfigurableOptions={showConfigurableOptions}
                      routeProductDetails={routeProductDetails}
                    />
                  </li>
                ))}
            </ul>
          </Fragment>
        );
      })}
    </Card>
  );
};
