import { OrderDataModel } from '@/order/data/models';
import { UseOrderProductListProps } from '@/order/types';
import { events } from '@adobe-commerce/event-bus';
import { useEffect, useState } from 'preact/hooks';

export const useOrderProductList = ({
  orderData,
}: UseOrderProductListProps) => {
  const [loading, setLoading] = useState(true);
  const [order, setOrder] = useState(orderData);

  useEffect(() => {
    const event = events.on(
      'order/data',
      (order: OrderDataModel) => {
        setOrder(order);
        setLoading(false);
      },
      { eager: true }
    );

    return () => {
      event?.off();
    };
  }, []);

  return { loading, order };
};
