import { OrderProductListContent } from '@/order/components';
import { useGetStoreConfig, useOrderProductList } from '@/order/hooks';
import { setTaxStatus } from '@/order/lib/setTaxStatus';
import { OrderProductListProps } from '@/order/types';
import { classes, Container } from '@adobe-commerce/elsie/lib';

export const OrderProductList: Container<OrderProductListProps> = ({
  slots,
  className,
  orderData,
  withHeader,
  showConfigurableOptions,
  routeProductDetails,
}) => {
  const storeConfig = useGetStoreConfig();
  const { loading, order } = useOrderProductList({
    orderData,
  });

  return (
    <div className={classes(['order-order-product-list', className])}>
      <OrderProductListContent
        slots={slots}
        loading={loading}
        placeholderImage={storeConfig?.baseMediaUrl ?? ''}
        taxConfig={setTaxStatus(storeConfig?.shoppingOrderDisplayPrice ?? 0)}
        order={order}
        withHeader={withHeader}
        showConfigurableOptions={showConfigurableOptions}
        routeProductDetails={routeProductDetails}
      />
    </div>
  );
};
