import { OrderDataModel, OrderItemModel } from '../data/models';

export const categorizeProducts = (order: OrderDataModel) => {
  const itemsList = order?.items ?? [];
  const returnedList = itemsList
    .filter((el) => el?.eligibleForReturn && el?.quantityReturnRequested)
    .map((item) => ({
      ...item,
      totalQuantity: item.quantityReturnRequested,
    }));

  const returnedListMap = new Map(returnedList.map((item) => [item.id, item]));
  const modifyOrder = itemsList
    .map((mainItem) => {
      const matchingItem = returnedListMap.get(mainItem?.id);

      if (matchingItem) {
        const quantityResult =
          mainItem.totalQuantity - matchingItem.quantityReturnRequested;

        if (quantityResult === 0) return null;

        return {
          ...mainItem,
          totalQuantity: quantityResult,
        };
      }

      return mainItem;
    })
    .filter((item): item is OrderItemModel => item !== null);

  return {
    returnedList,
    canceledItems: modifyOrder?.filter((element) => element.quantityCanceled),
    nonCanceledItems: modifyOrder?.filter(
      (element) => !element.quantityCanceled
    ),
  };
};
